.gutengeek-block-lib-item{box-shadow:0 1px 10px #dadde2;border:none;border-radius:3px;overflow:hidden;padding:0;text-align:center}.gutengeek-block-lib-item .card-image{position:relative;overflow:hidden}.gutengeek-block-lib-item .card-image .components-spinner{position:absolute;top:50%;left:50%;margin:0;transform:translate(-50%, -50%)}.gutengeek-block-lib-item .card-footer{padding:10px 10px}.gutengeek-block-lib-item.processing img{opacity:0.7}.gutengeek-block-lib-item.disabled{opacity:0.5}.gutengeek-block-lib-item img{transition:all .4s ease-in-out}.gutengeek-block-lib-item:hover{cursor:pointer}.gutengeek-block-lib-item:hover img{transform:translateY(-4px) scale(1.2)}.gutengeek-block-lib-item .pro-feature{position:absolute;top:10px;left:15px;display:block;padding:5px 12px;font-size:11px;font-weight:600;color:#fff;line-height:1;border-radius:2px;background-color:#F44336}
